<template><section>
<h1>checkbox复选框</h1>
<p>一组备选项中进行多选</p>
<h2>基础用法</h2>
<p>单独使用可以表示两种状态之间的切换，写在标签中的内容为 checkbox 按钮后的介绍。
在<code v-pre="">lee-checkbox</code>元素中定义<code v-pre="">v-model</code>绑定变量，单一的<code v-pre="">checkbox</code>中，默认绑定变量的值会是<code v-pre="">Boolean</code>，选中为<code v-pre="">true</code>。</p>
<div class="demo-block">
  <lee-checkbox v-model="checkbox">备选项</lee-checkbox>
</div>
<demo-block>
                        <div slot="desc">
<lee-checkbox v-model="checkbox" name="name">备选项</lee-checkbox>

</div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">lee-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkbox"</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"name"</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">lee-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">checkbox</span>: <span class="hljs-literal">true</span>
      };
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2>禁用状态</h2>
<p>多选框不可用状态。
设置<code v-pre="">disabled</code>属性即可。</p>
<div class="demo-block">
  <lee-checkbox disabled v-model="checkbox1" name="name1">备选项</lee-checkbox>
  <lee-checkbox disabled v-model="checkbox2" name="name2">备选项</lee-checkbox>
</div>
<demo-block>
                        <div slot="desc">
<lee-checkbox disabled v-model="checkbox1" name="name1">备选项</lee-checkbox>
<lee-checkbox disabled v-model="checkbox2" name="name2">备选项</lee-checkbox>

</div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">lee-checkbox</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkbox1"</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"name1"</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">lee-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">lee-checkbox</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkbox2"</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"name2"</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">lee-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">checkbox1</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">checkbox2</span>: <span class="hljs-literal">true</span>
      };
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2>多选框组</h2>
<p>适用于多个勾选框绑定到同一个数组的情景，通过是否勾选来表示这一组选项中选中的项。</p>
<p><code v-pre="">checkbox-group</code>元素能把多个 <code v-pre="">checkbox</code> 管理为一组，只需要在 Group 中使用<code v-pre="">v-model</code>绑定<code v-pre="">Array</code>类型的变量即可。 lee-checkbox 的 <code v-pre="">label</code>属性是该 checkbox 对应的值，若该标签中无内容，则该属性也充当 checkbox 按钮后的介绍。label与数组中的元素值相对应，如果存在指定的值则为选中状态，否则为不选中。</p>
<div class="demo-block">
  <p style="margin-bottom:10px;">{{checkbox3}}</p>
  <lee-checkbox-group v-model="checkbox3">
    <lee-checkbox name="name3" label="复选框A">复选框A</lee-checkbox>
    <lee-checkbox name="name3" label="复选框B">复选框B</lee-checkbox>
    <lee-checkbox disabled name="name3" label="复选框C">复选框C</lee-checkbox>
    <lee-checkbox disabled name="name3" label="复选框D">复选框D</lee-checkbox>
  </lee-checkbox-group>
</div>
<demo-block>
                        <div slot="desc">
<p style="margin-bottom:10px;">{{checkbox3}}</p>
<lee-checkbox-group v-model="checkbox3">
  <lee-checkbox name="name3" label="复选框A">复选框A</lee-checkbox>
  <lee-checkbox name="name3" label="复选框B">复选框B</lee-checkbox>
  <lee-checkbox disabled name="name3" label="复选框C">复选框C</lee-checkbox>
  <lee-checkbox disabled name="name3" label="复选框D">复选框D</lee-checkbox>
</lee-checkbox-group>

</div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">p</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"margin-bottom:10px;"</span>&gt;</span>{{checkbox3}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">lee-checkbox-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkbox3"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">lee-checkbox</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"name3"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"复选框A"</span>&gt;</span>复选框A<span class="hljs-tag">&lt;/<span class="hljs-name">lee-checkbox</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">lee-checkbox</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"name3"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"复选框B"</span>&gt;</span>复选框B<span class="hljs-tag">&lt;/<span class="hljs-name">lee-checkbox</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">lee-checkbox</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"name3"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"复选框C"</span>&gt;</span>复选框C<span class="hljs-tag">&lt;/<span class="hljs-name">lee-checkbox</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">lee-checkbox</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"name3"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"复选框D"</span>&gt;</span>复选框D<span class="hljs-tag">&lt;/<span class="hljs-name">lee-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">lee-checkbox-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">checkbox3</span>: [<span class="hljs-string">'复选框D'</span>]
      };
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2>indeterminate 状态</h2>
<p><code v-pre="">indeterminate</code> 属性用以表示 checkbox 的不确定状态，一般用于实现全选的效果</p>
<div class="demo-block">
  <lee-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange" style="margin-bottom:10px;">全选</lee-checkbox>
  <lee-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
    <lee-checkbox v-for="city in cities" :label="city" :key="city">{{city}}</lee-checkbox>
  </lee-checkbox-group>
</div>
<demo-block>
                        <div slot="desc">
<lee-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange" style="margin-bottom:10px;">全选</lee-checkbox>
<lee-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
  <lee-checkbox v-for="city in cities" :label="city" :key="city">{{city}}</lee-checkbox>
</lee-checkbox-group>

</div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">lee-checkbox</span> <span class="hljs-attr">:indeterminate</span>=<span class="hljs-string">"isIndeterminate"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkAll"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"handleCheckAllChange"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"margin-bottom:10px;"</span>&gt;</span>全选<span class="hljs-tag">&lt;/<span class="hljs-name">lee-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">lee-checkbox-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkedCities"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"handleCheckedCitiesChange"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">lee-checkbox</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"city in cities"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"city"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"city"</span>&gt;</span>{{city}}<span class="hljs-tag">&lt;/<span class="hljs-name">lee-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">lee-checkbox-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">const</span> cityOptions = [<span class="hljs-string">'上海'</span>, <span class="hljs-string">'北京'</span>, <span class="hljs-string">'广州'</span>, <span class="hljs-string">'深圳'</span>];
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">checkAll</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">checkedCities</span>: [<span class="hljs-string">'上海'</span>, <span class="hljs-string">'北京'</span>],
        <span class="hljs-attr">cities</span>: cityOptions,
        <span class="hljs-attr">isIndeterminate</span>: <span class="hljs-literal">true</span>
      };
    },
    <span class="hljs-attr">methods</span>: {
      handleCheckAllChange(val) {
        <span class="hljs-keyword">this</span>.checkedCities = val ? cityOptions : [];
        <span class="hljs-keyword">this</span>.isIndeterminate = <span class="hljs-literal">false</span>;
      },
      handleCheckedCitiesChange(value) {
        <span class="hljs-keyword">let</span> checkedCount = value.length;
        <span class="hljs-keyword">this</span>.checkAll = checkedCount === <span class="hljs-keyword">this</span>.cities.length;
        <span class="hljs-keyword">this</span>.isIndeterminate = checkedCount &gt; <span class="hljs-number">0</span> &amp;&amp; checkedCount &lt; <span class="hljs-keyword">this</span>.cities.length;
      }
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2>可选项目数量的限制</h2>
<p>使用 min 和 max 属性能够限制可以被勾选的项目的数量。</p>
<div class="demo-block">
  <lee-checkbox-group v-model="checkedCities1" :min="1" :max="3">
    <lee-checkbox v-for="city in cities" :label="city" :key="city">{{city}}</lee-checkbox>
  </lee-checkbox-group>
</div>
<demo-block>
