  <lee-checkbox v-for="city in cities" :label="city" :key="city">{{city}}</lee-checkbox>
</lee-checkbox-group>

</div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">lee-checkbox-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkedCities1"</span> <span class="hljs-attr">:min</span>=<span class="hljs-string">"1"</span> <span class="hljs-attr">:max</span>=<span class="hljs-string">"3"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">lee-checkbox</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"city in cities"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"city"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"city"</span>&gt;</span>{{city}}<span class="hljs-tag">&lt;/<span class="hljs-name">lee-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">lee-checkbox-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">const</span> cityOptions = [<span class="hljs-string">'上海'</span>, <span class="hljs-string">'北京'</span>, <span class="hljs-string">'广州'</span>, <span class="hljs-string">'深圳'</span>];
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">checkedCities1</span>: [<span class="hljs-string">'上海'</span>, <span class="hljs-string">'北京'</span>],
        <span class="hljs-attr">cities</span>: cityOptions
      };
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
